import { FC, useEffect, useState } from 'react';
import { LatLngLiteral } from 'leaflet';
import Plot from 'react-plotly.js';
import { getElevations, getDistance } from './utils/elevations';

type plotData = {
  x: number[];
  y: number[];
};

type propType = {
  points: LatLngLiteral[]; // 2
  ratio: number; // xy(1)
};

/**
 * plotly.js2
 * 2(./utils/elevations)
 * xyratio(12)
 * @param param0
 * @returns
 */
const CrossSectionGraph: FC<propType> = ({ points, ratio }) => {
  const [plotData, setPlotData] = useState<plotData>();

  useEffect(() => {
    if (points) {
      const [lat1, lng1] = [points[0].lat, points[0].lng];
      const [lat2, lng2] = [points[1].lat, points[1].lng];

      // 2
      (async () => {
        const elevations = await getElevations(lat1, lng1, lat2, lng2);
        const distance = getDistance(lat1, lng1, lat2, lng2) * 1000;

        // ()
        const distancePerUnit = distance / elevations.length;
        const xy: plotData = { x: [], y: [] };

        // (x:y:)
        elevations.forEach((v, i) => {
          xy.x.push(i * distancePerUnit);
          xy.y.push(v);
        });

        setPlotData(xy);
      })();
    }
  }, [points]);

  const margin = {
    // 
    l: 80,
    r: 10,
    b: 40,
    t: 30,
    pad: 4,
  };

  /**
   * <Plot> plotly.js
   * data 
   */
  return (
    <>
      {plotData && (
        <Plot
          data={[
            {
              x: plotData.x,
              y: plotData.y,
              type: 'scatter', // 
              mode: 'lines', // 
              line: { shape: 'spline' }, // spline
            },
          ]}
          layout={{
            width: 500,
            margin,
            xaxis: {
              // x
              title: '',
              ticksuffix: 'm',
              exponentformat: 'none', // 
              rangemode: 'tozero', // 0
            },
            yaxis: {
              // y
              scaleanchor: 'x', // 
              scaleratio: ratio, // x
              rangemode: 'tozero', // 0
              title: '',
              ticksuffix: 'm',
              exponentformat: 'none', // 
            },
          }}
        />
      )}
    </>
  );
};

export default CrossSectionGraph;
